/**
 *  userNameKey:+
 * userNameKey:value
 */
export function setSessionStorage(userNameKey, value) {
    sessionStorage.setItem(userNameKey,value);
}

/**
 *  userNameKey:+
 * userNameKey:
 */
export function getSessionStorage(userNameKey) {
    return JSON.parse(sessionStorage.getItem(userNameKey));
}

//
//len
export function randomString(len) {
len = len || 32;
var $chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';    /****oOLl,9gq,Vv,Uu,I1****/
var maxPos = $chars.length;
var pwd = '';
for (var i = 0; i < len; i++) {
pwd += $chars.charAt(Math.floor(Math.random() * maxPos));
}
return pwd;
}

//+
//len
export function randomStringOrTime(len) {
len = len || 32;
var $chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';    /****oOLl,9gq,Vv,Uu,I1****/
var maxPos = $chars.length;
var pwd = '';
for (var i = 0; i < len; i++) {
pwd += $chars.charAt(Math.floor(Math.random() * maxPos));
}
return pwd+(new Date().getTime());
}

// 
export function accAdd(num1,num2){
    var r1,r2,m;
    try{
        r1 = num1.toString().split('.')[1].length;
    }catch(e){
        r1 = 0;
    }
    try{
        r2=num2.toString().split(".")[1].length;
    }catch(e){
        r2=0;
    }
    m=Math.pow(10,Math.max(r1,r2));
    // return (num1*m+num2*m)/m;
    return Math.round(num1*m+num2*m)/m;
}

// 
export function accSub(num1,num2){
    var r1,r2,m,n;
    try{
        r1 = num1.toString().split('.')[1].length;
    }catch(e){
        r1 = 0;
    }
    try{
        r2=num2.toString().split(".")[1].length;
    }catch(e){
        r2=0;
    }
    m=Math.pow(10,Math.max(r1,r2));
    n=(r1>=r2)?r1:r2;
    return (Math.round(num1*m-num2*m)/m).toFixed(n);
}

// 
export function accMul(num1,num2){
    var m=0,s1=num1.toString(),s2=num2.toString();
    try{m+=s1.split(".")[1].length}catch(e){};
    try{m+=s2.split(".")[1].length}catch(e){};
    return Number(s1.replace(".",""))*Number(s2.replace(".",""))/Math.pow(10,m);
}

// 
export function accDiv(num1,num2){
    var t1,t2,r1,r2;
    try{
        t1 = num1.toString().split('.')[1].length;
    }catch(e){
        t1 = 0;
    }
    try{
        t2=num2.toString().split(".")[1].length;
    }catch(e){
        t2=0;
    }
    r1=Number(num1.toString().replace(".",""));
    r2=Number(num2.toString().replace(".",""));
    return (r1/r2)*Math.pow(10,t2-t1);
}

// 
//arrkey
export function maxArrObj(arr,key){
    if(arr.length==0){
        return 0;
    }
    let max=arr[0][key];
    arr.forEach(item=>{
        if(item[key] > max){
            max=item[key];
        }
    });
    return max;
    // return Math.max.apply(Math, arr.map(function(o) {return o[key]}));
}

// 
//arrkey
export function minArrObj(arr,key){
    if(arr.length==0){
        return 0;
    }
    let min=arr[0][key];
    arr.forEach(item=>{
        if(min==0 && item[key]!=0){     //
            min=item[key];
        }
        if(item[key] < min && item[key]!=0){    //
            min=item[key];
        }
    });
    return min;
    // return Math.min.apply(Math, arr.map(function(o) {return o[key]}));
}

//
//ab
export function calcPercent(a,b){
    var c=a/b*100;
    return Math.min(100, Math.max(1, c));
}

//
//numlen
export function numFormat(num,len) {
    // num=num && num != 0 ? Number(num).toFixed(len) : 0;
    num=num && num != 0 ? (Math.floor(Number(num) * Math.pow(10,len)) / Math.pow(10,len)).toFixed(len) : 0;
    //toFixedMath.floor(5)toFixed
    var c = (num.toString().indexOf ('.') !== -1) ? num.toLocaleString() : num.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
    return c;
}

//
//oldTnewT
export function timeDiff(oldT,newT) {
    var nowtime = new Date(oldT),
        endtime = new Date(newT);
    var lefttime = endtime.getTime() - nowtime.getTime(),
        leftd = Math.floor(lefttime/(1000*60*60*24)),
        lefth = Math.floor(lefttime/(1000*60*60)%24),
        leftm = Math.floor(lefttime/(1000*60)%60),
        lefts = Math.floor(lefttime/1000%60);
    // return leftd + "" + lefth + ":" + leftm + ":" + lefts;
    return `${leftm>=10 ? leftm : '0'+leftm}:${lefts>=10 ?lefts : '0'+lefts}`;
}
//key
//arrjudgeKey,mergeKey,type
export function mergeArr(arr,judgeKey,mergeKey,type){
    let result=[];
    arr.forEach(item=>{
        let index=-1;
        result.forEach((m,n)=>{
            if(m[judgeKey]==item[judgeKey] && m[type]==item[type]){
                index=n;
            }
        });
        if(index!=-1){
            result[index][mergeKey]+=item[mergeKey];
        }else{
            result.push(item);
        }
    });
    return result;
}

//url
export function getUrlParam(url,key) {
    var params={};
    url=decodeURIComponent(url);//base64
    if(url.split("?").length<=1){
        return '';
    }
    var arr=url.split("?")[1].split("&");
    arr.forEach(function(item){
        params[item.split("=")[0]]=item.split("=")[1];
    });
    return key?params[key]:params;
}

//
export function createAutoData(count,max,min){
    var autoData = new Array();
    var Range = max - min;
    for(var i=0;i<count;i++){
        var Rand = Math.random();
        autoData.push(min + Math.round(Rand * Range));
    }
    return autoData;
}
//
export function compare(prop){
    return function(a,b){
        var value1=a[prop];
        var value2=b[prop];
        return value1-value2;
    }
}

/**
* 
* timeStamp:
*/
export function getThisTime(timeStamp) {
    var myDate = timeStamp ? new Date(parseInt(timeStamp) * 1000) : new Date();
    function p(s) {
        return s < 10 ? '0' + s : s;
    }
    return {
        'year': myDate.getFullYear(),
        'month': myDate.getMonth() + 1,
        'date': myDate.getDate(),
        'h': myDate.getHours(),
        'm': myDate.getMinutes(),
        's': myDate.getSeconds(),
        'now': myDate.getFullYear() + '-' + p(myDate.getMonth() + 1) + "-" + p(myDate.getDate()) + " " + p(myDate.getHours()) + ':' + p(myDate.getMinutes()) + ":" + p(myDate.getSeconds())
    };
}

/**
 * 
 * containerjquery
 */
export function dragMoveX(container) {
    var flag;
    var downX;
    var scrollLeft;
    //
    $(document.body).on("mousedown", container, function (event) {
        // console.log('mousedown')
        flag = true;
        downX = event.clientX;
        scrollLeft = $(this).scrollLeft();
    });
    //
    $(document).on("mousemove", function (event) {
        // console.log('mousemove')
        if (flag) {
            var moveX = event.clientX;
            var scrollX = moveX - downX;
            // console.log("moveX" + moveX);
            // console.log("scrollX" + scrollX);
            if (scrollX < 0 && scrollLeft > 0) {
                $(container).scrollLeft(scrollLeft - scrollX)
            }
            else {
                $(container).scrollLeft(scrollLeft - scrollX)
            }
        }
    });
    //
    $(document).on("mouseup", function () {
        // console.log('mouseup')
        flag = false;
    });
    /**
     *  mouseout mouseleave mouseout 
     * mouseout
     * */
    //
    $(container).on("mouseleave", function (event) {
        // console.log('mouseleave')
        if (event.pageX < 0 || event.pageX > document.body.offsetWidth) {
            flag = false;
        }         
    });
}

/**
 * 
 * @param Function fn 
 * @param Number delay 
 * @param Number atleast 
 * @return Function 
 */
 export function throttle(fn, dalay, atleast = 0) {
    let timer = null
    let previous = null
    return (...args) => {
        let now = +new Date() //
            !previous ? now : previous
        if (atleast && now - previous > atleast) {
            fn.apply(this, args)
            // 
            previous = now
            clearTimeout(timer)
        } else {
            clearTimeout(timer)
            timer = setTimeout(() => {
                fn.apply(this, args)
                previous = null
            }, dalay)
        }
    }
}
//
export function getDateDiff(dateStr) {
    var publishTime = getDateTimeStamp(dateStr) / 1000,
    d_seconds,
    d_minutes,
    d_hours,
    d_days,
    timeNow = parseInt(new Date().getTime() / 1000),
    d,

    date = new Date(publishTime * 1000),
    Y = date.getFullYear(),
    M = date.getMonth() + 1,
    D = date.getDate(),
    H = date.getHours(),
    m = date.getMinutes(),
    s = date.getSeconds();
    //100
    if (M < 10) {
            M = '0' + M;
    }
    if (D < 10) {
            D = '0' + D;
    }
    if (H < 10) {
            H = '0' + H;
    }
    if (m < 10) {
            m = '0' + m;
    }
    if (s < 10) {
            s = '0' + s;
    }

    d = timeNow - publishTime;
    d_days = parseInt(d / 86400);
    d_hours = parseInt(d / 3600);
    d_minutes = parseInt(d / 60);
    d_seconds = parseInt(d);

    if (d_days > 0 && d_days < 3) {
            return d_days + '';
    } else if (d_days <= 0 && d_hours > 0) {
            return d_hours + '';
    } else if (d_hours <= 0 && d_minutes > 0) {
            return d_minutes + '';
    } else if (d_seconds < 60) {
            if (d_seconds <= 0) {
                    return '';
            } else {
                    return d_seconds + '';
            }
    } else if (d_days >= 3 && d_days < 30) {
            return M + '-' + D + ' ' + H + ':' + m;
    } else if (d_days >= 30) {
            return Y + '-' + M + '-' + D + ' ' + H + ':' + m;
    }
}

function getDateTimeStamp(dateStr) {
      // 2020/07/09 21:43:19.000  .000 iosfirefox
return Date.parse(dateStr.replace(/-/gi, "/"));
}

