/**
 * 
 * str
 */
function isNullorEmpty(str) {
    return (str == null || str == "" || str == undefined || str == "undefined") && (str != 0 || str != "0") ? true : false;
}
//
export function toFixed(val,len=0){
    if(isNullorEmpty(val)){
        return '-';
    }else{
        return val && val != 0 ? (Number(val)).toFixed(len) : '0';
        // return val && val != 0 ? (Math.floor(Number(val) * Math.pow(10,len)) / Math.pow(10,len)).toFixed(len) : '0';
    }
}

//isShowAdd
export function toPercent(val,len,isShowAdd=0){
    if(isNullorEmpty(val)){
        return '-';
    }else{
        if(val && val != 0){
            let value=(Math.floor(Number(val) * Math.pow(10,len)) / Math.pow(10,len)).toFixed(len);
            if(isShowAdd && value>0){
                value='+'+value;
            }
            return value+'%';
        }else{
            return '0'+'%';
        }
    }
}

//
//len,str
//{{openPositionContract | format(2,' ETH')}}isShowAdd
export function format(val,len=0,str='',isShowAdd=0){
    // console.log(val)
    if(val===""||val===undefined || isNaN(val)){
        return '-';
    }
    if(val===0){
        return 0+str;
    }else{
        let value=(Math.floor(Number(val) * Math.pow(10,len)) / Math.pow(10,len)).toFixed(len);
        if(isShowAdd && value>0){
            value='+'+value;
        }
        return value+str;
    }
}

//,leftStr='$'
export function numFormat(num,len,leftStr='') {
    if(isNullorEmpty(num)){
        return '-';
    }
    var isNegative=num>=0 ? false : true;
    if(isNegative){     //
        num=Math.abs(Number(num));
    }

    // var c=num && num != 0 ? Number(num).toFixed(len).replace(/(\d)(?=(\d{3})+\.)/g, '$1,') : '0';
    // var c=num && num != 0 ? (Math.floor(Number(num) * Math.pow(10,len)) / Math.pow(10,len)).toFixed(len).replace(/(\d)(?=(\d{3})+\.)/g, '$1,') : '0';
    var c=num && num != 0 ? toThousands((Math.floor(Number(num) * Math.pow(10,len)) / Math.pow(10,len)).toFixed(len)) : '0';

    c=leftStr+c;
    if(isNegative){     //
        c='-'+c;
    }
    return c;
}

//
function toThousands(num) {
    var result = '', counter = 0;
    var dot = String(num).indexOf(".");
    if(dot != -1){
         // alert("");
        // (indexOfsubstring)
        var dotCnt = String(num).substring(dot+1,num.length);
 
        // 
        num = String(num).split('.')[0]
        num = (num || 0).toString();
        for (var i = num.length - 1; i >= 0; i--) {
            counter++;
            result = num.charAt(i) + result;
            if (!(counter % 3) && i != 0) { result = ',' + result; }
        }
        result = result + '.' + dotCnt;
        return result;
        
    }else{
        // alert("");
        return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
    }
}

//
export function getDateDiff(dateStr) {
    if(!dateStr){
        return '';
    }
    var publishTime = getDateTimeStamp(dateStr) / 1000,
    d_seconds,
    d_minutes,
    d_hours,
    d_days,
    timeNow = parseInt(new Date().getTime() / 1000),
    d,

    date = new Date(publishTime * 1000),
    Y = date.getFullYear(),
    M = date.getMonth() + 1,
    D = date.getDate(),
    H = date.getHours(),
    m = date.getMinutes(),
    s = date.getSeconds();
    //100
    if (M < 10) {
            M = '0' + M;
    }
    if (D < 10) {
            D = '0' + D;
    }
    if (H < 10) {
            H = '0' + H;
    }
    if (m < 10) {
            m = '0' + m;
    }
    if (s < 10) {
            s = '0' + s;
    }

    d = timeNow - publishTime;
    d_days = parseInt(d / 86400);
    d_hours = parseInt(d / 3600);
    d_minutes = parseInt(d / 60);
    d_seconds = parseInt(d);

    if (d_days > 0 && d_days < 3) {
            return d_days + '';
    } else if (d_days <= 0 && d_hours > 0) {
            return d_hours + '';
    } else if (d_hours <= 0 && d_minutes > 0) {
            return d_minutes + '';
    } else if (d_seconds < 60) {
            if (d_seconds <= 0) {
                    return '';
            } else {
                    return d_seconds + '';
            }
    } else if (d_days >= 3 && d_days < 30) {
            return M + '-' + D + ' ' + H + ':' + m;
    } else if (d_days >= 30) {
            return Y + '-' + M + '-' + D + ' ' + H + ':' + m;
    }
}

function getDateTimeStamp(dateStr) {
      // 2020/07/09 21:43:19.000  .000 iosfirefox
return Date.parse(dateStr.replace(/-/gi, "/"));
}

