//->0
export function getColor(val,newVal,oldVal){
    if(!newVal || !oldVal){
        return ''
    }
    if(newVal-oldVal>0){
        return 'red'
    }
    if(newVal-oldVal<0){
        return 'green'
    }
    return '';
}